import Vue from 'vue'
import App from './App.vue'
import VueShowdown from 'vue-showdown'
import axios from 'axios'

Vue.prototype.$http = axios

Vue.use(VueShowdown, {
  flavor: 'github',
  options: {
    emoji: false,
  },
})

Vue.config.productionTip = false

new Vue({
  render: h => h(App),
}).$mount('#app')
