<template>
  <div id="app">
    <VueShowdown class="md" :markdown="md" flavor="github" :options="{ emoji: true }" />
  </div>
</template>

<script>
import "highlight.js/styles/dracula.css";
import hljs from "highlight.js";

export default {
  name: "App",
  components: {},
  methods: {
    encode(param) {
      try {
        return atob(decodeURIComponent(param));
      } catch (error) {
        // console.log(error);
      }
      return "";
    },
    query(variable) {
      let query = window.location.search.substring(1);
      let vars = query.split("&");
      for (let i = 0; i < vars.length; i++) {
        let pair = vars[i].split("=");
        if (pair[0] == variable) {
          return pair[1];
        }
      }
      return false;
    }
  },
  mounted() {
    let id = this.encode(this.query("id"));
    if (id) {
      this.$http.get("/data/" + id + '.md').then(result => {
        this.md = result.data;
        setTimeout(function() {
          [].forEach.call(
            document.querySelectorAll("pre code"),
            hljs.highlightBlock
          );
        }, 500);
      });
    }
    let bg = this.encode(this.query("bg"));
    if (bg) {
      bg = "rgba(" + bg + ", 0.15)";
      document
        .querySelector("body")
        .setAttribute("style", "background-color:" + bg);
    }
  },
  beforeCreate() {},
  data() {
    return {
      bg: "",
      md: ""
    };
  }
};
</script>

<style>
